import React, { useState } from "react"

const PlayPauseButton = ({ onToggle, style }) => {
  const [ongoing, setOngoing] = useState(false)

  return (
    <button
      style={style}
      onClick={() => {
        onToggle(!ongoing)
        setOngoing(!ongoing)
      }}
    >
      {ongoing ? "stop" : "go"}
    </button>
  )
}

export default PlayPauseButton
